<template>
  <div>
    <!-- 表单 -->
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="学号">
        <el-input placeholder="学号" v-model="sno"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="query">查询</el-button>
        <el-button type="primary"><router-link to="/admin/createstudent">新增学生</router-link></el-button>
      </el-form-item>
    </el-form>

    <!-- 表格 -->
    <el-table :data="tableData" border style="width: 100%">
      <el-table-column fixed prop="sno" label="学号" width="250"></el-table-column>
      <el-table-column prop="name" label="姓名" width="175"></el-table-column>
      <el-table-column prop="sex" label="性别" width="120"></el-table-column>
      <el-table-column prop="age" label="年龄" width="120"></el-table-column>
      <el-table-column prop="class" label="班级" width="120"></el-table-column>
      <el-table-column fixed="right" label="操作" width="200">
        <template v-slot="scope">
          <el-button @click="confirmRemove(scope.row)" type="text" size="small">删除</el-button>
          <el-button type="text" size="small" @click="edit(scope.row)">编辑</el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- 对话框 -->
    <el-dialog
      title="编辑学生信息"
      v-model="dialogVisible"
      width="30%"
      :before-close="handleClose">
      <el-form :model="editForm">
        <el-form-item label="学号">
          <el-input v-model="editForm.sno" disabled></el-input>
        </el-form-item>
        <el-form-item label="姓名">
          <el-input v-model="editForm.name"></el-input>
        </el-form-item>
        <el-form-item label="性别">
          <el-select v-model="editForm.sex" placeholder="请选择性别">
            <el-option label="男" value="男"></el-option>
            <el-option label="女" value="女"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="年龄">
          <el-input v-model="editForm.age"></el-input>
        </el-form-item>
        <el-form-item label="班级">
          <el-select v-model="editForm.class" placeholder="请选择班级">
            <el-option v-for="item in classOptions" :key="item[0]" :label="item[0]" :value="item[0]"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取消</el-button>
        <el-button type="primary" @click="saveEdit">保存</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      sno: '',
      dialogVisible: false,
      editForm: {
        sno: '',
        name: '',
        sex: '',
        age: '',
        class: ''
      },
      classOptions: []
    };
  },
  methods: {
    edit(row) {
      this.editForm = { ...row };
      this.dialogVisible = true;
      this.fetchClassOptions();
    },
    saveEdit() {
     let params = new URLSearchParams();
      params.append('sno', this.editForm.sno);
      params.append('name', this.editForm.name);
      params.append('sex', this.editForm.sex);
      params.append('age', this.editForm.age);
      params.append('class', this.editForm.class);
      
      this.$axios({
        method: "post",
        url: "http://127.0.0.1:8690/updatestudent",
        data: params,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
        }
      }).then((result) => {
        console.log(result.data);
        if(result.data.message=="successfully"){
            this.$message({
          type: 'success',
          message: '操作成功!'
        });
         this.dialogVisible=false
         let params = new URLSearchParams();
        this.$axios({
        method: "post",
        url: "http://127.0.0.1:8690/studentinfo",
        data: params,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
        }
      }).then((result) => {
        console.log(result.data);
        this.tableData = result.data.map(item => ({
        sno: item[0],
        name: item[1],
        sex: item[2],
        age: item[3],
        class: item[6],
      })); // 将获取到的课程数据存储到tableData数组中
    }).catch((error) => {
      console.error('请求失败:', error);
    });
        }else{
          this.$message({
          type: 'warning',
          message: '操作失败!'
        });
        }
      }).catch((error) => {
        console.error('请求失败:', error);
      });
    },
    fetchClassOptions() {
      this.$axios({
        method: "post",
        url: "http://127.0.0.1:8690/classnum",
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
        }
      }).then((result) => {
        console.log(result.data)
        this.classOptions = result.data;
      }).catch((error) => {
        console.error('请求失败:', error);
      });
    },
    confirmRemove(row) {
      this.$confirm(`此操作将永久删除学生 ${row.name}，是否继续？`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.remove(row);
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    remove(row) {
      let params = new URLSearchParams();
      params.append('sno', row.sno);
      this.$axios({
        method: "post",
        url: "http://127.0.0.1:8690/reamovestudent",
        data: params,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
        }
      }).then((result) => {
        console.log(result.data);
        this.query();
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
      }).catch((error) => {
        console.error('请求失败:', error);
        this.$message({
          type: 'warning',
          message: '删除失败!'
        });
      });
    },
    query() {
      if (!this.sno) {
        this.$message({
          type: 'warning',
          message: '请输入学号'
        });
        return;
      }
      let params = new URLSearchParams();
      params.append('sno', this.sno);
      this.$axios({
        method: "post",
        url: "http://127.0.0.1:8690/querystudent",
        data: params,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
        }
      }).then((result) => {
        console.log(result.data);
        this.tableData = [{
          sno: result.data[0],
          name: result.data[1],
          sex: result.data[2],
          age: result.data[3],
          class: result.data[6],
        }];
        this.$message({
          type: 'success',
          message: '查询成功!'
        });
      }).catch((error) => {
        console.error('请求失败:', error);
        this.$message({
          type: 'warning',
          message: '查询失败!'
        });
      });
    },
    handleClose(done) {
      this.dialogVisible = false;
    }
  },
  mounted() {
    let params = new URLSearchParams();
    this.$axios({
      method: "post",
      url: "http://127.0.0.1:8690/studentinfo",
      data: params,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
      }
    }).then((result) => {
      console.log(result.data);
      this.tableData = result.data.map(item => ({
        sno: item[0],
        name: item[1],
        sex: item[2],
        age: item[3],
        class: item[6],
      })); // 将获取到的课程数据存储到tableData数组中
    }).catch((error) => {
      console.error('请求失败:', error);
    });
  },
};
</script>

<style>
.el-button a {
  text-decoration: none;
  color: inherit;
}
</style>
